import React  from 'react';

function Experience(){
    return(
        <section className='experienceSection'>
            <div className='sectionTitle' id='experience'>Experience</div>
            <div className='experienceContainer'>
                <div className='experienceleft'>
                    <h2 className='experienceTitle'>Work Experience</h2>
                    <p>Web Dev (Jr/1/2) - Logical Position<br /> (05/2021 - Current)</p>
                    <p>Manager - CENTERS<br /> (08/2018 - 04/2021)</p>
                    <p>Grocery Associate - Heinen's Fine Foods<br /> (08/2015 - 03/2017)</p>
                </div>
                <div className='experienceright'>
                    <h2 className='experienceTitle'>Current Role</h2>
                    <p>I was hired over 2 years ago at the company Logical Position as a Jr Web Dev. Within this timespan, I've been able to improve my Web Dev capabilities. I'm now a Web Dev 2 with many more responsibilities. Such as troubleshooting, teaching other developers Javascript / React.js, as well as developing guidelines and instructions for future Devs.</p>
                    {/*<a href=''><button className='resumeButton'>Download Resume</button></a> */}
                </div>
            </div>
        </section>
    )
}

export default Experience;